import React, { Component } from "react";
import ReactDOM from "react-dom";
import HigherOrderComponent from "./Shared/HOC";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import HigherOrderComponentTable from "./Shared/HOCTable";
import RenderTable from "./Shared/RenderTable";
import GetAPIResult from "./Service/CallAPI";
class CheckPNR extends Component {
  constructor(props) {
    super(props);
    this.GetPNR = this.GetPNR.bind(this);
    this.state = {
      txtPNR: "",
      tbodyhtml: "....",
      htmltr: [],
    };
    console.log("handle constructor");
    console.log(this);
  }
  HandleChange = (e) => {
    console.log("handle info");
    console.log(this);
    this.setState({ [e.target.name]: e.target.value });
  };
  handleValidation() {
    if (this.state.txtPNR == "") return false;
    else return true;
  }
  GetPNR(e) {
    if (this.handleValidation() == false) return;
    const URl = `https://indianrailapi.com/api/v2/PNRCheck/apikey/56a0423e3f50c90936db4c6d3eda5819/PNRNumber/${this.state.txtPNR}/`;
    console.log("handle PNR");
    e.preventDefault();
    GetAPIResult(URl).then((res) => {
      if (res.data.ResponseCode == "201") {
        console.log(res);
        this.setState({
          tbodyhtml: "Invalid PNR Number,Try Again....",
        });
      } else {
      }
    });
  }

  render() {
    let html = HigherOrderComponent([
      {
        Type: "text",
        Id: "txtPNR",
        label: "PNR",
        handler: this.HandleChange,
        Name: "txtPNR",
        Placeholder: "Enter PNR",
        Mxlen: "10",
      },
    ]);
    let htmltable = HigherOrderComponentTable([
      "PnrNumber",
      "TrainNumber",
      "TrainName",
      "JourneyClass",
      "JourneyDate",
      "From",
      "To",
      "ChatPrepared",
    ]);
    let htmltbody = "";
    if (this.state.tbodyhtml == "") {
      htmltbody = "";
    } else {
      htmltbody = (
        <tr>
          <td colspan="8">{this.state.tbodyhtml}</td>
        </tr>
      );
    }
    return ReactDOM.createPortal(
      <div>
        <Form>
          {html}

          <Button variant="primary" type="submit" onClick={this.GetPNR}>
            Search
          </Button>
        </Form>

        <RenderTable htmltable={htmltable} tbodyhtml={htmltbody}></RenderTable>
      </div>,
      document.getElementById("portaldiv")
    );
  }
}

export default CheckPNR;
