import React, { Component } from "react";
import Table from "react-bootstrap/Table";
import Alert from "react-bootstrap/Alert";
import ReactDOM from "react-dom";
class Wikipidia extends Component {
  render() {
    let Heading = "";
    let RenderHtml = "";
    console.log(this.props.match.params.prm);
    if (this.props.match.params.prm == "TicketStatus") {
      Heading = "Railway Tiket Code with Description";
      RenderHtml = (
        <Table striped bordered hover responsive="sm">
          <thead>
            <th>Code</th>
            <th>Description</th>
          </thead>
          <tbody>
            <tr>
              <td>RAC</td>
              <td>
                {" "}
                In RAC (Reservation Against Cancellation), the passenger is
                allowed to travel and two passengers share the same berth. If a
                confirmed passenger does not board the train, a full berth is
                allotted to the passenger with RAC ticket.
              </td>
            </tr>
            <tr>
              <td>CNF</td>
              <td>
                {" "}
                The passenger seat has been confirmed and the seat will be
                allotted after charting
              </td>
            </tr>
            <tr>
              <td>CAN</td>
              <td>The passenger seat has been cancelled</td>
            </tr>
            <tr>
              <td>WL </td>
              <td>
                {" "}
                The passenger with Waitlist ticket is in the waiting list and is
                not allowed to board the train. A waitlist ticket can be
                cancelled by the passenger 30 minutes before the departure of
                the train. If a WL ticket does not get confirmed, it gets
                cancelled automatically.
              </td>
            </tr>
            <tr>
              <td>GNWL </td>
              <td>
                A General Waitlist ticket which gets confirmed after the
                passengers cancel their confirmed bookings
              </td>
            </tr>
            <tr>
              <td>TQWL</td>
              <td>
                TQWL stands for Tatkal Waitlist. When a passenger does a tatkal
                booking and is put on the waitlist, the status is shown as TQWL.
                This ticket is least likely to get confirmed.
              </td>
            </tr>
            <tr>
              <td>PQWL</td>
              <td>
                Under Pooled Quota Waitlist, passengers travelling between
                intermediate stations and have a separate waitlist from the
                general waitlist.
              </td>
            </tr>
            <tr>
              <td>RLWL </td>
              <td>
                A Remote Location Waitlist has high chances of confirmation.
                Smaller stations have quota of seats and waiting seats on these
                intermediate stations are given RLWL status.
              </td>
            </tr>
            <tr>
              <td>RSWL </td>
              <td>
                A station specific waitlist is called a Road-Side Waitlist.
              </td>
            </tr>
          </tbody>
        </Table>
      );
    } else if (this.props.match.params.prm == "TicketQuota") {
      Heading = "Railway Quota with Description";
      RenderHtml = (
        <Table striped bordered hover responsive="sm">
          <thead>
            <th>S.No.</th>
            <th>Code</th>
            <th>Description</th>
          </thead>

          <tbody>
            <tr>
              <td>1.</td>
              <td>GN</td>
              <td> General Quota</td>
            </tr>
            <tr>
              <td>2.</td>

              <td>LD</td>
              <td> Ladies Quota</td>
            </tr>
            <tr>
              <td>3.</td>
              <td>HO</td>

              <td> Head quarters/high official Quota</td>
            </tr>
            <tr>
              <td>4.</td>
              <td>DF</td>
              <td> Defence Quota</td>
            </tr>
            <tr>
              <td>5.</td>
              <td>PH</td>
              <td> Parliament house Quota</td>
            </tr>
            <tr>
              <td>6.</td>
              <td>FT</td>
              <td> Foreign Tourist Quota</td>
            </tr>
            <tr>
              <td>7.</td>
              <td>DP</td>
              <td> Duty Pass Quota</td>
            </tr>
            <tr>
              <td>8.</td>

              <td>tq</td>
              <td> Tatkal Quota</td>
            </tr>
            <tr>
              <td>9.</td>
              <td>PT</td>

              <td> Premium Tatkal Quota</td>
            </tr>
            <tr>
              <td>10.</td>
              <td>SS</td>
              <td> Female(above 45 Year)/Senior Citizen/Travelling alone</td>
            </tr>
            <tr>
              <td>11.</td>
              <td>HP</td>
              <td> Physically Handicapped Quota</td>
            </tr>
            <tr>
              <td>12.</td>
              <td>RE</td>
              <td> Railway Employee Staff on Duty for the train</td>
            </tr>
            <tr>
              <td>13.</td>
              <td>GNRS</td>
              <td> General Quota Road Side</td>
            </tr>
            <tr>
              <td>14.</td>
              <td>OS</td>
              <td> Out Station</td>
            </tr>
            <tr>
              <td>15.</td>
              <td>PQ</td>
              <td> Pooled Quota</td>
            </tr>
            <tr>
              <td>16.</td>
              <td>RC(RAC)</td>
              <td> Reservation Against Cancellation</td>
            </tr>
            <tr>
              <td>17.</td>
              <td>RS</td>
              <td> Road Side</td>
            </tr>{" "}
            <tr>
              <td>18.</td>
              <td>YU</td>
              <td> Yuva</td>
            </tr>
            <tr>
              <td>19.</td>
              <td>LB</td>
              <td> Lower Berth</td>
            </tr>
          </tbody>
        </Table>
      );
    } else if (this.props.match.params.prm == "TravelClasses") {
      Heading = "Railway Travel Classes with Description";
      RenderHtml = (
        <Table striped bordered hover responsive="sm">
          <thead>
            <th>Code</th>
            <th>Class</th>
            <th>Description</th>
            <th>Berth/Seat</th>
            <th>Tatkal</th>
            <th>Bedding</th>
          </thead>
          <tbody>
            <tr>
              <td>1A</td>
              <td>AC First Class</td>
              <td>
                Most expensive class, with fares that can be at par with airline
                fares in some cases. It has spacious, carpeted and lockable
                compartments with a washbasin. Showers and toilets are available
                at either end of the carriage. Sheets, pillows, blankets, and
                towels are provided. Berth and coach number are allocated only
                at the time of chart preparation. You cannot specify if you want
                accomodation in a 2 berth or 4 berth compartment when booking.
                Couples are usually given preference for 2-berth compartments,
                while families and passengers travelling alone are usually
                allocated berths in one of the 4-berth compartments but this
                cannot be guaranteed.
              </td>
              <td>2-berth/ 4-berth</td>
              <td>No</td>
              <td>Yes</td>
            </tr>
            <tr>
              <td>2A</td>
              <td>AC 2-Tier</td>
              <td>
                AC 2-tier is found on almost all long-distance trains and is
                slightly cheaper than AC First Class and around 25% more
                expensive than non-AC First Class (FC). Coaches are
                airconditioned with sleeping berths, curtains and individual
                reading lamps. Berths are arranged in cubicles of four (two
                upper, two lower) on one side of the aisle, and two above and
                below the windows on the other side of the aisle. Toilet
                facilities are available at either end of the carriage. Sheets,
                pillows and blankets are provided in the evenings.
              </td>
              <td>2-berth/ 4-berth</td>
              <td>Yes</td>
              <td>Yes</td>
            </tr>
            <tr>
              <td>3A</td>
              <td>AC 3-Tier</td>
              <td>
                Slightly cheaper than First Class, AC 3-tier is similar to AC
                2-tier, but it is more crowded than AC 2-tier, and it lacks the
                privacy curtains and individual reading lamps found in AC
                2-tier. It has three tiers of bunksupper, middle and
                lowerarranged in bays of six on one side of the aisle, and bays
                of two (side-upper and side-lower) on the other side of the
                aisle. During the day, the middle beds must be folded down flat
                against the compartment walls to allow passengers to sit on the
                lower beds. Toilet facilities are available at either end of the
                carriage. Pillows, sheets and blankets are provided in the
                evening.
              </td>
              <td>2-berth/ 6-berth</td>
              <td>Yes</td>
              <td>Yes</td>
            </tr>
            <tr>
              <td>FC</td>
              <td>First Class</td>
              <td>
                This is the way the Raj traveled, First Class has mostly been
                phased out in favor of Air Conditioned carriages. The fare is
                generally three times the Sleeper (SL) fare. It offers non-AC
                coaches with lockable 4-berth (two up &amp; two down) and
                2-berth (one up &amp; one down) compartments. Bedding is not
                included in the fare, but may be available for a small extra
                charge if booked in advance. Showers and toilets are available
                at either end of the carriage.
              </td>
              <td>2-berth/ 4-berth</td>
              <td>No</td>
              <td>Extra</td>
            </tr>
            <tr>
              <td>CC</td>
              <td>AC Chair Car</td>
              <td>
                Generally three times more expensive than Second Sitting (2S),
                the AC Chair Class is normally found on daytime shuttle or
                intercity trains.
                There are a total of five seats in a row, three on one side and
                two on the other side of aisle. Only sitting room is provided,
                with no standees allowed.
              </td>
              <td>2-seat/ 3-seat</td>
              <td>Yes</td>
              <td>No</td>
            </tr>
            <tr>
              <td>SL</td>
              <td>Sleeper</td>
              <td>
                Arguably the most popular class of travel, Sleeper has three
                tiers of bunksupper, middle and lowerarranged in bays of six
                on one side of the aisle, and bays of two (side-upper and
                side-lower) on the other side of the aisle. During the day, the
                middle beds must be folded down flat against the compartment
                walls to allow passengers to sit on the lower beds. Toilet
                facilities are available at either end of the carriage. Bedding
                is not provided.
              </td>
              <td>2-berth/ 6-berth</td>
              <td>Yes</td>
              <td>No</td>
            </tr>
            <tr>
              <td>2S</td>
              <td>Second Sitting</td>
              <td>
                The cheapest class of travel on the Indian Railways, Second
                Sitting has ordinary coaches which have seating arrangement on
                cushioned seats in two rows of 3 seats each.
              </td>
              <td></td>
              <td>No</td>
              <td>No</td>
            </tr>
          </tbody>
        </Table>
      );
    }
    return ReactDOM.createPortal(
      <div>
        <Alert variant="info">{Heading}</Alert>
        {RenderHtml}
      </div>,
      document.getElementById("portaldivtable")
    );
  }
}

export default Wikipidia;
