import React from "react";
import Form from "react-bootstrap/Form";
import { Col, InputGroup, Row } from "react-bootstrap";
// import TextField from 'material-ui/TextField';  
// import AutoComplete from 'material-ui/AutoComplete';  



export default function HigherOrderComponent(arr) {
  let htmlappender = "";
  const html = arr.map((myhtml) => {
    if (myhtml.Type == "text") {
      htmlappender = (
        <Form.Group as={Row} controlId={myhtml.Id}>
          <Form.Label column sm={4} style={{textAlign:'right'}}>
            {myhtml.label}
          </Form.Label>
          <Col sm={6}>
            <InputGroup hasValidation>
              <InputGroup.Prepend>
                <InputGroup.Text>*</InputGroup.Text>
              </InputGroup.Prepend>
              <Form.Control
                type="text"
                placeholder={myhtml.Placeholder}
                name={myhtml.Name}
                onChange={myhtml.handler}
                maxLength={myhtml.Mxlen}
                autoComplete="off"
                required
              />
            </InputGroup>
          </Col>
        </Form.Group>
      );
    } else if (myhtml.Type == "date") {
      htmlappender = (
        <Form.Group as={Row} controlId={myhtml.Id}>
          <Form.Label column sm={4} style={{textAlign:'right'}}>
            {myhtml.label}
          </Form.Label>
          <Col sm={6}>
            <InputGroup hasValidation>
              <InputGroup.Prepend>
                <InputGroup.Text>*</InputGroup.Text>
              </InputGroup.Prepend>
              <Form.Control
                type="date"
                placeholder={myhtml.Placeholder}
                name={myhtml.Name}
                onChange={myhtml.handler}
                maxLength={myhtml.Mxlen}
                required
              />
            </InputGroup>
          </Col>
        </Form.Group>
      );
    } else if (myhtml.Type == "Button") {
      htmlappender = null;
    } else if (myhtml.Type == "select") {
      htmlappender = (
        <Form.Group as={Row} controlId={myhtml.Id}>
          <Form.Label column sm={4} style={{textAlign:'right'}}>
            {myhtml.label}
          </Form.Label>
          <Col sm={6}>
            <InputGroup hasValidation>
              <InputGroup.Prepend>
                <InputGroup.Text>*</InputGroup.Text>
              </InputGroup.Prepend>
              <Form.Control
                as="select"
                placeholder={myhtml.Placeholder}
                name={myhtml.Name}
                onChange={myhtml.handler}
                maxLength={myhtml.Mxlen}
                required
              >
              <option value="-1" key="-1">
                Select
              </option>
              {myhtml.Val}
              </Form.Control>
            </InputGroup>
          </Col>
        </Form.Group>
      );
    } else {
   


  

    }
    console.log(HigherOrderComponent);

    return htmlappender;
  });
  return html;
}
