

import React, { Component } from "react";
import ReactDOM from "react-dom";
import SharedApi from "./Service/SharedApi";
import HigherOrderComponentTable from "./Shared/HOCTable";
import RenderTable from "./Shared/RenderTable";
;
class StationMaster extends Component {
  
 

  render() {
    
   let htmltbody=SharedApi.GetAllStationName().sort().map((myMap,index)=>{
     
     return <tr><td>{index+1}</td><td>{myMap.StationCode}</td><td>{myMap.StationName}</td></tr>
   })
    let htmltable = HigherOrderComponentTable([
      "S.No.",
      "StationCode",
      "StationName",
      
    ]);
 
   
    return ReactDOM.createPortal(
      <div>
        

        <RenderTable htmltable={htmltable} tbodyhtml={htmltbody}></RenderTable>
      </div>,
      document.getElementById("portaldiv")
    );
  }
}

export default StationMaster;
