import React, { Component } from "react";
import ReactDOM from "react-dom";
import HigherOrderComponentTable from "./Shared/HOCTable";
import RenderTable from "./Shared/RenderTable";
import GetAPIResult from "./Service/CallAPI";
import Alert from "react-bootstrap/Alert";
import HOCTableSearch from "./Shared/HOCTableSearch";
import Form from "react-bootstrap/Form";
import SpinerLoader from "./Shared/SpinerLoader";

class StationName extends Component {
  constructor(props) {
    console.log("cons");

    super(props);
    //this.Loadtbl=this.Loadtbl.bind(this)
    this.state = {
      tbodyhtml: "....",
      htmltr: [],
      Globaltr: [],
      showspiner: true,
    };
  }
  HandleChange = (e) => {
    this.setState({ [e.target.name]: e.target.value }, () => {
      console.log(this.state.htmltr);
    });
  };
  GetTrainInformation() {
    // if (this.handleValidation() == false) return;

    const URl = `https://indianrailapi.com/api/v2/${this.props.match.params.prm}/apikey/56a0423e3f50c90936db4c6d3eda5819/`;
    console.log("handle GetTrainInformation");
    
    GetAPIResult(URl).then((res) => {
      if (res.data.ResponseCode == "201") {
        console.log(res);
        this.setState({
          tbodyhtml: "Invalid response,Try Again....",
        });
      } else {
        console.log(res);
        res.data.Trains.push({ TrainNumber: "0", TrainName: " " });
        this.setState(
          {
            htmltr: res.data.Trains,
            tbodyhtml: "",
            Globaltr: res.data.Trains,
            showspiner: false,
          },
          () => {}
        );
      }
    });
  }
  componentDidMount() {
    console.log("mount");
    this.GetTrainInformation();
  }
  componentDidUpdate() {
    console.log("Update");
    // this.GetTrainInformation();
  }
  LoadtblTrainNumber = (e) => {
    if (e.target.value == "") {
      this.setState(
        {
          htmltr: this.state.Globaltr,
        },
        () => {}
      );
    } else {
      this.setState(
        {
          htmltr: this.state.htmltr.filter(
            (myfilter) =>
              myfilter.TrainNumber == e.target.value ||
              myfilter.TrainNumber == "0"
          ),
        },
        () => {
          console.log(this.state.htmltr);
        }
      );
      console.log("fucker");
    }
  };
  render() {
    console.log("render");
    let tblSearch = HOCTableSearch([
      "S.No.",
      "Train Number",
      "Train Name",
      "Source",
      "Destination",
      "Arrival",
    ]);
    let htmltable = HigherOrderComponentTable([
      "S.No.",
      "Train Number",
      "Train Name",
      "Source",
      "Destination",
      "Arrival",
    ]);
    let htmltbody = "";
    let Index = 0;
    let Returnrow = 0;
    if (this.state.tbodyhtml == "") {
      htmltbody = this.state.htmltr.reverse().map((mytr) => {
        console.log(mytr);
        if (mytr.TrainNumber == "0") {
          Returnrow = Returnrow + 1;

          return (
            <tr>
              <td></td>
              <td>
                <Form.Control
                  type="text"
                  name="TrainNumber"
                  onChange={this.HandleChange}
                  onBlur={this.LoadtblTrainNumber}
                />
              </td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
            </tr>
          );
        } else {
          Index = Index + 1;

          return (
            <tr>
              <td>{Index}</td>
              <td>{mytr.TrainNumber}</td>
              <td>{mytr.TrainName}</td>
              <td>{mytr.Source}</td>
              <td>{mytr.Destination}</td>
              <td>{mytr.Arrival}</td>
            </tr>
          );
        }
      });
    } else {
      htmltbody = (
        <tr>
          <td colspan="7">{this.state.tbodyhtml}</td>
        </tr>
      );
    }
    console.log(this.props.match.params.prm);

    return ReactDOM.createPortal(
      <div>
        {this.state.showspiner ? (
          <SpinerLoader />
        ) : (
          <div>
           
            <Alert variant="info">
              Details for {this.props.match.params.prm}
            </Alert>{" "}
            <RenderTable
              htmltable={htmltable}
              tbodyhtml={htmltbody}
            ></RenderTable>
          </div>
        )}
        {/* <RenderTable htmltable={htmltable} tbodyhtml={htmltbody}></RenderTable> */}
      </div>,
      document.getElementById("portaldiv")
    );
  }
}

export default StationName;
