import React, { Component } from "react";
import ReactDOM from "react-dom";
import HigherOrderComponent from "./Shared/HOC";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import HigherOrderComponentTable from "./Shared/HOCTable";
import RenderTable from "./Shared/RenderTable";
import GetAPIResult from "./Service/CallAPI";
import Alert from "react-bootstrap/Alert";
import SharedApi from "./Service/SharedApi";
class TrainInquiry extends Component {
  constructor(props) {
    super(props);
    this.GetInformation = this.GetInformation.bind(this);

    this.state = {
      txttrainNumber: "",
      tbodyhtml: "....",
      htmltr: [],
      Source: [],
      Destination: [],
      StationName: "",
    };
    console.log("handle constructor");
    console.log(this);
  }

  HandleChange = (e) => {
    console.log("handle info");
    console.log(this);
    this.setState({ [e.target.name]: e.target.value });
  };
  handleValidation() {
    if (this.state.txttrainNumber == "") return false;
    else return true;
  }

  componentDidUpdate()
  {
    console.log("update")
  }

  GetInformation(e) {
    if (this.handleValidation() == false) return;
    e.preventDefault();
    let URl = "";
    let id = e.target.id;
    if (id == 2) {
      console.log("e", e.target.id);
      URl = `https://indianrailapi.com/api/v2/AutoCompleteTrainInformation/apikey/56a0423e3f50c90936db4c6d3eda5819/TrainNumberOrName/${this.state.txttrainNumber}/`;
      console.log("handle handlne trani");

      GetAPIResult(URl).then((res) => {
        if (res.data.ResponseCode == "201") {
          console.log(res);
          this.setState({
            tbodyhtml: "Invalid response,Try Again....",
          });
        } else {
         
          console.log(res.data.Trains);
          this.setState({
          
            htmltr: res.data.Trains,
            tbodyhtml: "",
          });
        }
      });
    }
  }

 

  
  render() {
    
    
    let Heading = "";
    let html = "";
    let buttonId = "";
    let htmltable = "";
    let htmltbody = "";
    
    console.log("Source",this.state.Source)
  
    if (this.props.match.params.prm == "RescheduledTrains") {
      Heading = "Search Rescheduled Trains.. ";
      buttonId = "0";
      html = HigherOrderComponent([
        {
          Type: "text",
          Id: "txttrainNumber",
          label: "Enter Date",
          handler: this.HandleChange,
          Name: "txttrainNumber",
          Placeholder: "Enter Date",
          Mxlen: "12",
        },
      ]);
    } else if (this.props.match.params.prm == "CancelledTrains") {
      Heading = "Search Cancelled Trains..";
      buttonId = "1";
      html = HigherOrderComponent([
        {
          Type: "text",
          Id: "txttrainNumber",
          label: "Enter Date",
          handler: this.HandleChange,
          Name: "txttrainNumber",
          Placeholder: "Enter Date",
          Mxlen: "13",
        },
      ]);
    } else if (this.props.match.params.prm == "TrainInfo") {
      Heading = "Search Train by Train Number/Name...";
      buttonId = "2";
      html = HigherOrderComponent([
        {
          Type: "text",
          Id: "txttrainNumber",
          label: "Train Number/Name",
          handler: this.HandleChange,
          Name: "txttrainNumber",
          Placeholder: "Enter Train Number or Name",
          Mxlen: "13",
        },
      ]);
      htmltable = HigherOrderComponentTable([
        "TrainNo",
        "TrainName",
        "Source",
        "Source Arrival",
        "Destination",
        "Destination Arrival",
      ]);
      if (this.state.tbodyhtml == "") {
       
        htmltbody = this.state.htmltr.map((mytr,index) => {
         let myStationSource=SharedApi.GetStationName(mytr.Source.Code)
         let myStationDestination=SharedApi.GetStationName(mytr.Destination.Code)
          return (
           
            <tr>
              <td>{mytr.TrainNo}</td>
              <td>{mytr.TrainName}</td>
              <td>{myStationSource.length>0?myStationSource[0].StationName:mytr.Source.Code}</td>
              <td>{mytr.Source.Arrival}</td>
              <td> {myStationDestination.length>0?myStationDestination[0].StationName:mytr.Destination.Code}</td>
              <td>{mytr.Destination.Arrival}</td>
            </tr>
          );

        });
      } else {
        htmltbody = (
          <tr>
            <td colspan="6">{this.state.tbodyhtml}</td>
          </tr>
        );
      }
    }

    // let htmltable = HigherOrderComponentTable([
    //   "PnrNumber",
    //   "TrainNumber",
    //   "TrainName",
    //   "JourneyClass",
    //   "JourneyDate",
    //   "From",
    //   "To",
    //   "ChatPrepared",
    // ]);
    // let htmltbody = "";
    // if (this.state.tbodyhtml == "") {
    //   htmltbody = "";
    // } else {
    //   htmltbody = (
    //     <tr>
    //       <td colspan="8">{this.state.tbodyhtml}</td>
    //     </tr>
    //   );
    // }
    return ReactDOM.createPortal(
      <div>
        <Alert variant="info">{Heading}</Alert>
        <Form>
          {html}

          <Button
            variant="primary"
            type="submit"
            onClick={this.GetInformation}
            id={buttonId}
          >
            Search
          </Button>
        </Form>

        {
          <RenderTable
            htmltable={htmltable}
            tbodyhtml={htmltbody}
          ></RenderTable>
        }
      </div>,
      document.getElementById("portaldiv")
    );
  }
}

export default TrainInquiry;
