import Form from "react-bootstrap/Form";
import { Col, Row } from "react-bootstrap";
export default function HOCTableSearch(arr) {
  const html = arr.map((myhtml) => {
    return (
      <th>
        

        <Form.Control type="text" name={myhtml} id={myhtml}/>
        
      </th>
    );
  });
  return html;
}
