import React, { Component } from "react";
import ReactDOM from "react-dom";
import HigherOrderComponent from "./Shared/HOC";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import HigherOrderComponentTable from "./Shared/HOCTable";
import RenderTable from "./Shared/RenderTable";
import GetAPIResult from "./Service/CallAPI";
import Alert from "react-bootstrap/Alert";

import { Col, InputGroup, Row } from "react-bootstrap";
import autocomplete from "./Shared/AutoCmplete";

class SeatAvl extends Component {
  constructor(props) {
    super(props);
    this.GetSeatAvl = this.GetSeatAvl.bind(this);
    this.state = {
      txttrainNumber: "",
      tbodyhtml: "....",
      txtDate: "",
      txtFrom: "",
      txtTo: "",
      htmltr: [],
      Fromstation: [],
      ddlClass: "0",
      TrainNumber: "",
      Source: "",
      Destination: "",
      Class: "",
      ClassOptn: [
        { Id: "3A", Value: "3A" },
        { Id: "2A", Value: "2A" },
        { Id: "1A", Value: "1A" },
        { Id: "SL", Value: "SL" },
      ],
    };
    console.log("handle constructor");
    console.log(this);
  }
  HandleChange = (e) => {
    console.log("handle info");
    console.log(this);
    console.log(e.target.name);
    this.setState({ [e.target.name]: e.target.value }, () => {
      if (e.target.name.toString() == "txtFrom") {
        if (e.target.value.length > 2) {
          this.FillFromStation();
        }
      }
      if (e.target.name.toString() == "txtTo") {
        if (e.target.value.length > 2) {
          this.FillToStation();
        }
      }
    });
  };

  FillFromStation() {
    const URl = `https://indianrailapi.com/api/v2/AutoCompleteStation/apikey/56a0423e3f50c90936db4c6d3eda5819/StationCodeOrName/${this.state.txtFrom}/`;
    console.log("handle Stations");
    let arayFrom = [];
    GetAPIResult(URl).then((res) => {
      console.log(res);
      this.setState(
        {
          Source: autocomplete(
            document.getElementById("txtFrom"),
            res.data.Station.map((mapvalue) => {
              // console.log(mapvalue.NameEn)
              return mapvalue.NameEn;
            }),res.data.Station
          ),
        },
        () => {
          console.log("fucker", this.state.Source);
        }
      );
    });
  }
  FillToStation() {
    const URl = `https://indianrailapi.com/api/v2/AutoCompleteStation/apikey/56a0423e3f50c90936db4c6d3eda5819/StationCodeOrName/${this.state.txtTo}/`;
    console.log("handle Stations");
    let arayFrom = [];
    GetAPIResult(URl).then((res) => {
      console.log(res);
      this.setState(
        {
          Destination: autocomplete(
            document.getElementById("txtTo"),
            res.data.Station.map((mapvalue) => {
              // console.log(mapvalue.NameEn)
              return mapvalue.NameEn;
            }),res.data.Station
          ),
        },
        () => {
          console.log("fucker", this.state.Destination);
        }
      );
    });
  }

  GetSeatAvl(e) {
   // if (this.handleValidation() == false) return;
    let dateSplit = this.state.txtDate.split("-");
    let FetchDate = dateSplit[0] + dateSplit[1] + dateSplit[2];
    console.log(this.state.txtFrom);
    const URl = `https://indianrailapi.com/api/v2/SeatAvailability/apikey/56a0423e3f50c90936db4c6d3eda5819/TrainNumber/${this.state.txttrainNumber}/From/${this.state.Source.StationCode}/To/${this.state.Destination.StationCode}/Date/${FetchDate}/Quota/GN/Class/${this.state.ddlClass}`;
    console.log("handle PNR");
    e.preventDefault();
    GetAPIResult(URl).then((res) => {
      if (res.data.ResponseCode == "201") {
        console.log(res);
        this.setState({
          tbodyhtml: "Invalid response,Try Again....",
        });
      } else {
        console.log(res);
        this.setState({
          htmltr: res.data.Availability,
          tbodyhtml: "",
          TrainNumber: res.data.TrainNo,
          Source: res.data.From,
          Destination: res.data.To,
          Class: res.data.ClassCode,
        });
      }
    });
  }
  handleValidation() {
    if (this.state.txttrainNumber == "") return false;
    else if (this.state.txtDate == "") return false;
    else if (this.state.txtFrom == "") return false;
    else if (this.state.txtTo == "") return false;
    else if (this.state.ddlClass == "0") return false;
    else return true;
  }
  render() {
    /*An array containing all the country names in the world:*/

    let html = HigherOrderComponent([
      {
        Type: "text",
        Id: "txttrainNumber",
        label: "Train Number",
        handler: this.HandleChange,
        Name: "txttrainNumber",
        Placeholder: "Enter Train Number",
        Mxlen: "6",
      },
      {
        Type: "text",
        Id: "txtFrom",
        label: "Source Station",
        handler: this.HandleChange,
        Name: "txtFrom",
        Placeholder: "Enter Source Station/Code",
        Mxlen: "100",
      },
      {
        Type: "text",
        Id: "txtTo",
        label: "Destination Station",
        handler: this.HandleChange,
        Name: "txtTo",
        Placeholder: "Enter Destination Station/Code",
        Mxlen: "100",
      },
      {
        Type: "date",
        Id: "txtDate",
        label: "Date",
        handler: this.HandleChange,
        Name: "txtDate",
        Placeholder: "Enter Date",
        Mxlen: "10",
      },
      {
        Type: "select",
        Id: "ddlClass",
        label: "Class",
        handler: this.HandleChange,
        Name: "ddlClass",
        Placeholder: "Select Class",
        Mxlen: "10",
        Val: this.state.ClassOptn.map((info) => (
          <option value={info.Id} key={info.Id}>
            {info.Value}
          </option>
        )),
      },
    ]);
    let htmltable = HigherOrderComponentTable([
      "TrainNumber",
      "JourneyDate",
      "Source",
      "Destination",
      "Class",
      "Availability",
      "Confirm%",
    ]);
    let htmltbody = "";
    if (this.state.tbodyhtml == "") {
      console.log("objHeader", this.state.objHeader);
      htmltbody = this.state.htmltr.map((mytr) => {
        return (
          <tr>
            <td>{this.state.TrainNumber}</td>
            <td>{mytr.JourneyDate}</td>
            <td>{this.state.Source}</td>
            <td>{this.state.Destination}</td>
            <td>{this.state.Class}</td>
            <td>{mytr.Availability}</td>
            <td>{mytr.Confirm}</td>
          </tr>
        );
      });
    } else {
      htmltbody = (
        <tr>
          <td colspan="7">{this.state.tbodyhtml}</td>
        </tr>
      );
    }
    return ReactDOM.createPortal(
      <div>
        <Form>
          {html}

          <Button variant="primary" type="submit" onClick={this.GetSeatAvl}>
            Search
          </Button>
          <br />
          <br />
          <Alert variant="info">
            Currently we are fetching data for General Quata only...
          </Alert>
        </Form>

        <RenderTable htmltable={htmltable} tbodyhtml={htmltbody}></RenderTable>
      </div>,
      document.getElementById("portaldiv")
    );
  }
}

export default SeatAvl;
