import React, { Component } from "react";
import ReactDOM from "react-dom";
import HigherOrderComponentTable from "./Shared/HOCTable";
import HigherOrderComponent from "./Shared/HOC";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import RenderTable from "./Shared/RenderTable";
import GetAPIResult from "./Service/CallAPI";
import Alert from "react-bootstrap/Alert";
import SpinerLoader from "./Shared/SpinerLoader";
import HigherOrderComponentTabletr from "./Hoctr";
class LiveStatus extends Component {
  constructor(props) {
    super(props);
    this.GetTrainStatus = this.GetTrainStatus.bind(this);
    this.state = {
      txttrainNumber: "",
      tbodyhtml: "....",
      txtDate: "",
      Alerttext: "",
      show: false,
      htmltr: [],
      showspiner: true,
    };
  }
  HandleChange = (e) => {
    console.log("handle info");
    console.log(this);
    this.setState({ [e.target.name]: e.target.value });
  };
  handleValidation() {
    if (this.state.txttrainNumber == "") return false;
    else if (this.state.txtDate == "") return false;
    else return true;
  }
  GetTrainStatus(e) {
    if (this.handleValidation() == false) {
      this.setState(
        {
          show: true,
          Alerttext: "Fill all fields mark with *..",
        },
        () => {
          return;
        }
      );
      return;
    }
    let dateSplit = this.state.txtDate.split("-");
    let FetchDate = dateSplit[0] + dateSplit[1] + dateSplit[2];
    console.log(dateSplit[0], dateSplit[1], dateSplit[2]);
    e.preventDefault();

    const URl = `https://indianrailapi.com/api/v2/livetrainstatus/apikey/56a0423e3f50c90936db4c6d3eda5819/trainnumber/${this.state.txttrainNumber}/date/${FetchDate}/`;
    console.log("handle GetTrainStatus");
    GetAPIResult(URl).then((res) => {
      let Alertvalue = "Train  has not started yet..";
      if (res.data.CurrentStation != null) {
        Alertvalue = `Train has reached ${res.data.CurrentStation.StationName} on ${res.data.CurrentStation.ActualArrival}`;
      }
      console.log(res);
      if (res.data.ResponseCode == "201") {
        this.setState({
          tbodyhtml: "Invalid Train Number or Date,Try Again....",
          show: false,
          showspiner: false,
        });
      } else {
        this.setState({
          show: true,
          Alerttext: Alertvalue,
          htmltr: res.data.TrainRoute,
          tbodyhtml: "",
          showspiner: false,
        });
      }
    });
  }

  render() {
    let html = HigherOrderComponent([
      {
        Type: "text",
        Id: "txttrainNumber",
        label: "Train Number",
        handler: this.HandleChange,
        Name: "txttrainNumber",
        Placeholder: "Enter Train Number",
        Mxlen: "6",
      },
      {
        Type: "date",
        Id: "txtDate",
        label: "Date",
        handler: this.HandleChange,
        Name: "txtDate",
        Placeholder: "Enter Date",
        Mxlen: "10",
      },
    ]);
    let htmltable = HigherOrderComponentTable([
      "Sl.No.",
      "Station Name[Code]",
      "ScheduleArrival",
      "ActualArrival",
      "ScheduleDeparture",
      "ActualDeparture",
    ]);
    let htmltbody = "";
    if (this.state.tbodyhtml == "") {
      htmltbody = this.state.htmltr.map((mytr) => {
        return (
          <tr>
            <td>{mytr.SerialNo}</td>
            <td>
              {mytr.StationName}({mytr.StationCode})
            </td>
            <td>{mytr.ScheduleArrival}</td>
            <td>{mytr.ActualArrival}</td>
            <td>{mytr.ScheduleDeparture}</td>
            <td>{mytr.ActualDeparture}</td>
          </tr>
        );
      });
    } else {
      htmltbody = (
        <tr>
          <td colspan="6">{this.state.tbodyhtml}</td>
        </tr>
      );
    }
    return ReactDOM.createPortal(
      <div>
        <Form>
          {html}
          <Button variant="primary" type="submit" onClick={this.GetTrainStatus}>
            Search
          </Button>
          <br /> <br />
          <Alert variant="info" show={this.state.show}>
            {this.state.Alerttext}
          </Alert>
        </Form>
       
      <RenderTable htmltable={htmltable} tbodyhtml={htmltbody}></RenderTable> 
      </div>,
      document.getElementById("portaldiv")
    );
  }
}

export default LiveStatus;
